<template>
  <div class="love-oath">
    <Banner></Banner>

    <div class="row my-oath issue-an-oath">
      <h2></h2>
      <form @submit="onSubmit" @reset="onReset">
        <div class="thumbnail">
          <div class="con4">
            <canvas id="cvs" width="220" height="220"></canvas>
            <span class="btn upload"><input type="file" class="upload_pic" id="upload" v-on:change="getFile" required></span>
            <div class="prompt">300*300</div>
          </div>
        </div>
        <div class="caption">
          <div class="form-group">
            <label for="exampleInputEmail1">*</label>
            <input type="text" class="form-control" v-model="form.title" maxlength="50" placeholder="50" required>
          </div>
          <div class="form-group">
            <label for="exampleInputEmail1">*</label>
            <textarea class="form-control" rows="3" v-model="form.message" maxlength="500" placeholder="500" required></textarea>
          </div>
          <div class="form-group">
            <button type="reset" class="btn"></button>
            <button type="submit" class="btn btn-default"></button>
          </div>
        </div>
      </form>
    </div>
  </div>
</template>
<script >
import api from '../libs/api'
import image from '../libs/image'
import Banner from "./Banner";

export default {
  name: 'CreateLove',
  components: {Banner},
  data () {
    return {
      form: {
        title: '',
        message: '',
        file: ''
      }
    }
  },
  created () {
    setTimeout("window.scrollTo(0,800)",2000)
  },
  methods: {
    onSubmit (evt) {
      evt.preventDefault()
      let self = this
      image.PhotoCompress(this.form.file, {
        quality: 0.8,
        width: 320,
      }, re => {
        api.publishLove(this.form.title, this.form.message, re, resp => {
          self.$router.push({path: '/'})
        })
      })
    },
    onReset (evt) {
      evt.preventDefault()
      this.form.title = ''
      this.form.message = ''
      this.form.file = ''
    },
    getFile($event){
      this.form.file = $event.target.files[0] //
      if(!/image\/\w+/.test(this.form.file.type)){
        //typeimage/pngimage/jpg
        alert("");
        return false;
      }
      // console.log(file);
      var reader = new FileReader();//
      reader.readAsDataURL(this.form.file);//url
      //
      reader.onload = function(e){

        var image = new Image();
        // src
        image.src = e.target.result;
        var max=200;
        // load
        image.onload = function(){
          //  canvas DOM 
          var canvas = document.getElementById("cvs");
          //   *=
          /*if(image.height > max) {
                    image.width *= max / image.height;
                    image.height = max;
                } */
          //  canvas 2d ,
          var ctx = canvas.getContext("2d");
          // canvas
          ctx.clearRect(0, 0, canvas.width, canvas.height);
          // canvas
          /*canvas.width = image.width;
                canvas.height = image.height;
                if (canvas.width>max) {canvas.width = max;}*/
          // canvas
          // ctx.drawImage(image, 0, 0, image.width, image.height);
          ctx.drawImage(image, 0, 0, 220, 220);
          // imagedom
        };
      }
    }
  }
}
</script>



